import { BrandsImages } from '@/interfaces/brands';
import { query } from './strapi';

export function getBrands() {
  const { STRAPI_HOST } = process.env;
  return query('marcas?populate[imagenes][fields][0]=url').then((res) => {
    const marcas = res.data;
    const images = marcas[0]?.imagenes ?? ([] as BrandsImages[]);

    const newImages = images.map((img: BrandsImages) => ({
      id: img.id,
      url: `${STRAPI_HOST}${img.url}`,
    }));
    return newImages;
  });
}
