import { BrandsImagesProps } from '@/interfaces/brands';
import '@/components/brands.css';

export const Brands = ({ images }: BrandsImagesProps) => {
  const title = 'Conf√≠an en mi';
  const duplicatedImages = [...images, ...images];

  return (
    <section className="pt-24">
      <h1 className="mt-24 text-center text-4xl font-semibold uppercase md:text-6xl">
        {title}
      </h1>
      <div className="relative overflow-hidden py-24">
        <div className="animate-scroll stop-animation flex w-max">
          {duplicatedImages.map((image, index) => (
            <img
              key={`${image.id}-${index}`}
              src={image.url}
              alt={`Brand ${image.id}`}
              className="mx-6 w-auto flex-shrink-0 sm:h-12 md:h-16"
            />
          ))}
        </div>
      </div>
    </section>
  );
};
